export default {
	data() {
		return {
			type: 0, //类别
			loadMoreText: '加载中...',
			showLoadMore: false,
			isMore: true,
			PageNo: 0,
			PageSize: 10,
			mainItem: [{
				Date: '2021-04-10 13:22',
				Num: "￥3"
			}, {
				Date: '2021-04-10 13:22',
				Num: "￥3"
			}, {
				Date: '2021-04-10 13:22',
				Num: "￥3"
			}, {
				Date: '2021-04-10 13:22',
				Num: "￥3"
			}, {
				Date: '2021-04-10 13:22',
				Num: "￥3"
			}, {
				Date: '2021-04-10 13:22',
				Num: "￥3"
			}],
			money:100,//本月个人业绩
			keyWords: '' //搜索关键词
		};
	},
	onShow() {
		//传参
		var shotType = uni.getStorageSync('shotType');
		if (shotType != '' && parseInt(shotType) > 0) {
			this.type = parseInt(shotType);
			uni.setStorage({
				key: 'shotType',
				data: 0
			});
			//查询数据
		}
	},
	onLoad(option) {
		//this.initData();

	},
	onUnload() {
		(this.PageNo = 0), (this.mainItem = []), (this.loadMoreText = '加载更多'), (this.showLoadMore = false);
	},
	onReachBottom() {
		//下拉加载
		this.showLoadMore = true;
		setTimeout(() => {
			this.setListData();
		}, 300);
	},
	onPullDownRefresh() {
		//上拉刷新
		this.initData();
	},
	methods: {
		search(res) {
			this.keyWords = res.value;
			this.initData();
		},
		dumpInfo(id) {
			uni.navigateTo({
				url: '/pages/shop/info?id=' + id
			});
		},
		initData() {
			//刷新数据
			setTimeout(() => {
				this.PageNo = 0;
				this.mainItem = [];

				this.getPage();
				uni.stopPullDownRefresh();
			}, 300);
		},
		setListData() {
			//加载更多数据
			this.PageNo += 1;
			if (this.isMore) this.getPage();
		},
		getPage() {

		}
	}
};
